  height: 45.55px;
  left: 413px;
  top: 877px;
  animation-name: rot;  /* задаём имя анимации (соответствует имени назначенному в ключевых кадрах)  */
  animation-duration: 2.5s;  /* задаём продолжительность анимации */
  animation-iteration-count: infinite;
}
@keyframes rot {
  0%   {opacity: 1;}
  25%  {opacity: 1;}
  50%  {top: 892px}
  70% {opacity: 0}
  100% {top: 877px}
}
